.quadro-letras {
    justify-content: center;
    align-items: center;
    display: flex;
}

.letra {
    text-align: center;
    border: 2px solid rgb(79, 94, 108);
    border-radius: 5px;
    font-size: 20px;
    margin: 10px;
    width: calc(100% - 10px);
    max-width: 50px;
}

.teclado {
    display: grid;
    grid-template-columns: repeat(10, 35px);
    flex-direction: column;
    gap: 2.2px;
   justify-content: center;
}

